import './index.css';
import ImageGrid from './components/ImageGrid';
import Lightbox from './components/Lightbox';
import SearchBox from './components/SearchBox';
import LoadMoreButton from './components/LoadMoreButton';
import ImageLoadController from './components/ImageLoadController';
import WindowWrapper from './components/WindowWrapper';
import DocumentWrapper from './components/DocumentWrapper';

document.addEventListener('DOMContentLoaded', function () {

  const loadMoreButton = new LoadMoreButton(
    document.getElementById('loadMoreBtn'),
    document.getElementById('loadingStatus'),
  );
  const searchBox = new SearchBox(
    document.getElementById('searchBox'),
    document.getElementById('searchBtn'),
  );
  const imageGrid = new ImageGrid(
    document.getElementById('imageGrid')
  );
  const lightbox = new Lightbox(
    document.getElementById('lightboxOverlay'),
    document.getElementById('lightboxClose'),
    document.getElementById('lightbox'),
    document.getElementById('lb-loader'),
    document.getElementById('lb-image'),
    document.getElementById('lb-prev'),
    document.getElementById('lb-next'),
    document.getElementById('lb-caption'),
    document.getElementById('lb-outerContainer'),
    document.getElementById('lb-dataContainer'),
  );

  const wrappedDocument = new DocumentWrapper(document);
  const wrappedWindow = new WindowWrapper(window);

  const controller = new ImageLoadController(
    searchBox,
    imageGrid,
    lightbox,
    loadMoreButton,
    wrappedDocument,
    wrappedWindow,
  );

  controller.firstLoad();
});



