import {
  $show,
  $hide,
} from '../utils/utils';

class Lightbox {
  constructor(
    $overlay,
    $lightboxClose,
    $lightbox,
    $lightboxLoader,
    $lightboxImage,
    $prevButton,
    $nextButton,
    $lightboxText,
    $lightboxOuterContainer,
    $lightboxDataContainer
  ) {
    this.$overlay = $overlay;
    this.$lightboxClose = $lightboxClose;
    this.$lightbox = $lightbox;
    this.$lightboxLoader = $lightboxLoader;
    this.$lightboxImage = $lightboxImage;
    this.$prevButton = $prevButton;
    this.$nextButton = $nextButton;
    this.$lightboxText = $lightboxText;
    this.$lightboxOuterContainer = $lightboxOuterContainer;
    this.$lightboxDataContainer = $lightboxDataContainer;
    this.lightboxImage = null;

    this.$overlay.addEventListener('click', () => { this.close(); }, false);
    this.$lightboxClose.addEventListener("click", () => { this.close(); }, false);
    this.$lightbox.addEventListener('click', (e) => {
      if (e.target.matches('.lb-next')) {
        this.setImage(this.lightboxImage.next);
      } else if (e.target.matches('.lb-prev')) {
        this.setImage(this.lightboxImage.prev);
      } else if (e.target.matches('.lb-nav')) {
        // no-op;
      } else {
        this.close();
      }
    }, false);
  }

  open() {
    $show(this.$overlay);
    $show(this.$lightboxClose);
    $show(this.$lightbox);
    this.resizeOverlay();
    this.positionLightbox();
    this.disableScrolling();
  }

  close() {
    $hide(this.$overlay);
    $hide(this.$lightboxClose);
    $hide(this.$lightbox);
    this.enbleScrolling();
  }

  resizeOverlay() {
    this.$overlay.style.width = `${document.body.clientWidth}px`;
    this.$overlay.style.height = `${document.body.clientHeight}px`;
  };

  positionLightbox() {
    const top = Math.floor(window.scrollY);
    this.$lightbox.style.top = `${top + 100}px`;
  }

  disableScrolling() {
    document.body.style.overflow = 'hidden';
  }

  enbleScrolling() {
    document.body.style.overflow = null;
  }

  resizeLightbox(width, height) {
    const border = 4;
    this.$lightboxImage.style.width = `${width}px`;
    this.$lightboxImage.style.height = `${height}px`;
    this.$lightboxOuterContainer.style.width = `${width + 2 * border}px`;
    this.$lightboxOuterContainer.style.height = `${height + 2 * border}px`;
    this.$lightboxDataContainer.style.width = `${width}px`;
  }

  setImage(image) {
    $show(this.$lightboxLoader);
    $hide(this.$lightboxImage);
    this.lightboxImage = image;
    this.$lightboxImage.src = image.link.replace(image.id, `${image.id}l`);
    image.prev ? $show(this.$prevButton) : $hide(this.$prevButton);
    image.next ? $show(this.$nextButton) : $hide(this.$nextButton);
    this.$lightboxText.innerHTML = image.title;
    if (image.width > image.height) {
      const scaledWidth = 500;
      const scaledHeight = Math.floor(scaledWidth * image.height / image.width);
      this.resizeLightbox(scaledWidth, scaledHeight);
    } else {
      const scaledHeight = 350;
      const scaledWidth = Math.floor(scaledHeight * image.width / image.height);
      this.resizeLightbox(scaledWidth, scaledHeight);
    }

    this.$lightboxImage.onload = () => {
      $hide(this.$lightboxLoader);
      $show(this.$lightboxImage);
    };
  }
}

export default Lightbox;


