import Observable from '../utils/Observable';
import {
  $show,
  $hide,
} from '../utils/utils';

const events = {
  click: 'click'
};

class LoadMoreButton extends Observable {
  constructor (
    $loadMoreBtn,
    $loadingStatus,
  ) {
    super();
    this.$loadMoreBtn = $loadMoreBtn;
    this.$loadingStatus = $loadingStatus;
    this.isHidden = false;
    this.isLoading = false;
    this.$loadMoreBtn.onclick = () => {
      this.broadcast(events.click);
    };
  }

  update() {
    if (this.isHidden) {
      $hide(this.$loadMoreBtn);
      $hide(this.$loadingStatus);
    } else if (this.isLoading) {
      $hide(this.$loadMoreBtn);
      $show(this.$loadingStatus);
    } else {
      $show(this.$loadMoreBtn);
      $hide(this.$loadingStatus);
    }
  }

  show() {
    this.isHidden = false;
    this.update();
  }

  hide() {
    this.isHidden = true;
    this.update();
  }

  setIsLoading(isLoading) {
    this.isLoading = isLoading;
    this.update();
  }

  reset() {
    this.isHidden = false;
    this.isLoading = false;
    this.update();
  }

  addClickLoadMoreButtonEventListener(listener) {
    this.addObserver(events.click, listener);
  }
}

export default LoadMoreButton;



